  align-items: center;
  text-align: center;
  padding: 0 20px;
}

.intro-title .name {
  position: absolute;
  top: 25px;
  left: 30px;
  font-weight: 700;
  color: #007bff;
  font-size: 1.5rem;
}

@media screen and (max-width: 480px) {
  .intro-title .name {
    top: 20px;
    left: 25px;
    font-size: 1.25rem;
  }
}

.intro-title h1 {
  font-size: 4rem;
  margin: 10px 0;
}

.intro-title h2 {
  font-size: 1.5rem;
}

@media screen and (max-width: 480px) {
  .intro-title h2 {
    font-size: 1.25rem;
  }
}

@media screen and (max-width: 360px) {
  .intro-title h2 {
    font-size: 1.1rem;
  }
}

.intro-title a {
  font-weight: 700;
  color: #007bff !important;
  position: relative;
  display: inline-block;
}

.intro-title a:after {
  position: absolute;
  top: 100%;
  left: 0;
  width: 100%;
  height: 3px;
  background: #007bff;
  content: '';
  opacity: 0;
  -webkit-transition: height 0.3s, opacity 0.3s, -webkit-transform 0.3s;
  transition: height 0.3s, opacity 0.3s, -webkit-transform 0.3s;
  transition: height 0.3s, opacity 0.3s, transform 0.3s;
  transition: height 0.3s, opacity 0.3s, transform 0.3s, -webkit-transform 0.3s;
  -webkit-transform: translateY(-10px);
  transform: translateY(-10px);
}

.intro-title a:focus:after,
.intro-title a:hover:after {
  height: 3px;
  opacity: 1;
  -webkit-transform: translateY(-5px);
  transform: translateY(-5px);
}
